import { useMutation, useQuery } from '@tanstack/react-query';
import React, { useEffect, useState, useRef } from 'react';
import { useRecoilValue } from 'recoil';
import { getPidInfo, updatePidInfo } from '../../../api/Dojang/dojangApi';
import PageContainer from '../../../components/layout/PageContainer';
import BlackBtnSM from '../../../components/UI/Atoms/Button/BlackBtnSM';
import EditBtnMD from '../../../components/UI/Atoms/Button/EditBtnMD';
import EditBtnSM from '../../../components/UI/Atoms/Button/EditBtnSM';
import { useDebug } from '../../../hooks/basic/useDebug';
import dojangIdAtom from '../../../recoil/atom/dojangIdAtom';
import InfoBox from '../Manage/Dojang/DojangInfo/components/InfoBox';
import { InfoInput, InfoLabel } from '../Manage/Dojang/DojangInfo/components/InfoLable';
import { Col, Row } from '../Manage/Student/component/StudentInfo/StudentForm/StudentForm';

const PIDPage = () => {
  const dojangId = useRecoilValue(dojangIdAtom);

  const ref = useRef();

  const [edit, setEdit] = useState(false);

  const { data, refetch } = useQuery(['getPidInfo', dojangId], () => getPidInfo(dojangId), {
    onSuccess: (res) => {
      // console.log('get : ' + res.PID);
    },
  });

  const [editData, setEditData] = useState(data);

  const { mutate: updateMT } = useMutation((body) => updatePidInfo(dojangId, body), {
    onSuccess: (res) => {
      // console.log('update : ' + res);
    },
  });

  const handleChange = (e) => {
    setEditData({ PID: e.target.value });
  };

  const handleCancel = () => {
    setEdit(false);
  };

  const handleSave = () => {
    updateMT(editData, {
      onSuccess: (res) => {
        console.log(res);
        refetch();
        alert('PID 등록/수정 완료');
      },
    });
    setEdit(false);
  };

  const handleEdit = () => {
    setEdit(true);
  };

  useEffect(() => {
    setEditData(data);
  }, [data]);

  //   useDebug(data);

  return (
    <>
      <PageContainer>
        <InfoBox
          title="PID 정보"
          className="w-[500px] mt-[-10px] m-auto border border-[1px] border-solid border-[#eeeeee] border-t-0 border-x-0 rounded-none"
          btn={
            edit ? (
              <div className="flex gap-2 mb-[10px]">
                <EditBtnSM className="w-[70px] h-[30px]" onClick={handleCancel}>
                  취소
                </EditBtnSM>
                <BlackBtnSM className="w-[70px] h-[30px]" onClick={handleSave}>
                  등록
                </BlackBtnSM>
              </div>
            ) : (
              <div className="flex gap-2 mb-[10px]">
                <EditBtnMD className="w-[60px] h-[30px]" onClick={handleEdit}>
                  수정
                </EditBtnMD>
              </div>
            )
          }
        >
          <div>
            <Row>
              <Col className="mt-[10px] mb-[40px]">
                <InfoLabel>PID</InfoLabel>
                <div>
                  <InfoInput
                    name="PID"
                    value={editData?.PID || ''}
                    onChange={handleChange}
                    disabled={!edit}
                  />
                </div>
              </Col>
            </Row>
          </div>
        </InfoBox>
        <div className="mt-[40px] text-[#aaaaaa] leading-8">
          * 발급받은 PID를 등록해 주세요. <br /> * PID를 등록해야 모바일앱에서 수련비 결제를 받을 수
          있습니다.
        </div>
      </PageContainer>
    </>
  );
};

export default PIDPage;
